import React, { useState, useEffect } from 'react';
import articleContent from './Article-Content';
import ArticlesList from '../components/ArticlesList';
import NotFoundPage from './NotFoundPage';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentsForm from '../components/AddCommentForm';

const ArticlePage = ({ match }) => {
	const name = match.params.name;
	//after adding :name to the url
	//the prop called match will have a params with the var

	const article = articleContent.find((article) => article.name === name);

	const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] }); //changing the state with hooks

	//useEffect lets fetching data and display result
	//executes whenever articleInfo loaded
	useEffect( () => {
		const fetchData = async () => {
			const result = await fetch(`/api/articles/${name}`);
			const body = await result.json();
			console.log(body);
			setArticleInfo(body);
		}
		fetchData();
	}, [name]);
	//array means if something in it changes, call useeffect again

	if (!article) return <NotFoundPage></NotFoundPage>;

	const otherArticles = articleContent.filter((article) => article.name !== name);

	return (
		<React.Fragment>
			<h1>{article.title}</h1>
			<UpvotesSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo}></UpvotesSection>
			{article.content.map((paragraph, key) => (
				<p key={key}>{paragraph}</p>
			))}
			<CommentsList comments={articleInfo.comments}></CommentsList>
			<AddCommentsForm articleName={name} setArticleInfo={setArticleInfo}></AddCommentsForm>
			<h3>Other Articles</h3>
			<ArticlesList articles={otherArticles}></ArticlesList>
		</React.Fragment>
	);
};

export default ArticlePage;
